.App {
  display: flex;
  justify-content: space-between;
  padding: 20px;
}

.left-panel,
.right-panel {
  width: 45%;
}

.left-panel {
  padding-right: 20px;
}

.right-panel {
  border: 1px solid black;
  font-size: larger;
}

textarea {
  width: 100%;
  height: 300px;
  font-size: 16px;
  border: none;
  margin: 2px;
  border: 1px solid black;
}

.highlighted {
  color: black;
  cursor: pointer;
  margin-left: 2px;
}

.highlighted.active {
  color: white;
  border-bottom: 1px solid black;
  padding-left: 2px;
  padding-right: 2px;
}

.special-character {
  position: relative;
  margin: 0px;
  padding: 0px;
}

.arrow {
  position: relative;
  font-size: 24px;
  margin: 5px;
  top: 120px;
  cursor: pointer;
}
